import { app, Component } from 'apprun';
export default class extends Component {
  view = _ => `_html:<h1 id="async-fetch">Async Fetch</h1>
<p>AppRun supports asynchronous operations in the AppRun event handlers. We only need to add the async keyword in front of the event handler. The event handler can call the functions that return a Promise object using the await keyword.</p>
<p>See a component that displays random XKCD pictures using async fetch.</p>
<p><my-xkcd></my-xkcd></p>
<p>The code of the component is listed below.</p>
<pre><code class="language-javascript">import { app, Component, customElement } from 'apprun';

@customElement('my-xkcd')
export default class extends Component {
  state = {};

  view = (state) =&gt; &lt;&gt;
    &lt;div&gt;&lt;button $onclick='fetchComic'&gt;XKCD&lt;/button&gt;&lt;/div&gt;
    {state.loading ? &lt;div&gt;loading ... &lt;/div&gt; : ''}
    {state.comic &amp;&amp; &lt;&gt;
      &lt;h3&gt;{state.comic.title}&lt;/h3&gt;
      &lt;img src={state.comic.url} /&gt;
    &lt;/&gt;}
  &lt;/&gt;;

  update = {
    'loading': (state, loading) =&gt; ({ ...state, loading }),
    'fetchComic': async _ =&gt; {
      this.run('loading', true);
      const response = await fetch('https://xkcd-imgs.herokuapp.com/');
      const comic = await response.json();
      this.run('loading', false);
      return { comic };
    }
  };
}
</code></pre>
<p>Using the async fetch, you can bring in data from other systems in to AppRun Site apps as easy as the example above.</p>
<p>AppRun Site apps also have a service worker include to cache the fetch requests. The tool can be used to build a lightening fast app (even for dynamic content) that works offline.</p>
<p>Learn how to make a <a href="#wpa">Progress Web App</a>.</p>
`;
}