import { app, Component } from 'apprun';
export default class extends Component {
  view = _ => `_html:<h1 id="how-it-works">How It Works</h1>
<p>You put your AppRun components in the <em>pages</em> directory structure as following:</p>
<pre><code>src/
  pages/
    home.tsx
    contact.tsx
    about.tsx
</code></pre>
<p>You then run the <em>build</em> script.</p>
<pre><code class="language-sh">npx apprun-site build
</code></pre>
<p>You can also run the <em>build</em> script in the watch mode.</p>
<pre><code class="language-sh">npx apprun-site build -w
</code></pre>
<p>The <em>build</em> scripts creates the <strong>src/_lib/index.tsx</strong> file that contains the routing events and the components.</p>
<pre><code class="language-javascript">// this file is auto-generated
import _about_0 from './about_tsx';
import _contact_1 from './contact_tsx';
import _home_2 from './home_tsx';
export default [
  [&quot;/about&quot;, _about_0],
  [&quot;/contact&quot;, _contact_1],
  [&quot;/home&quot;, _home_2],
] as (readonly [string, any])[];
</code></pre>
<p>In addition to having AppRun components as pages, you can also add HTML files and markdown files in the <em>pages</em> directory. The <em>build</em> scripts converts them into AppRun components and saves to the <strong>src/_lib</strong> directory.</p>
<p>The generated <strong>src/_lib/index.tsx</strong> imports the all components from <strong>src/_lib</strong> and <strong>src/pages</strong>; and exports an array that maps routing events to components.</p>
<p>The main entry point of the application, <strong>src/index.tsx</strong>, imports the event-component mapping from <strong>src/_lib/index.tsx</strong>. Then it creates the pages, and sets the routing events.</p>
<p>Next, let's <a href="#getting-started">Getting Started</a> to see it in action.</p>
`;
}