import { app, Component } from 'apprun';
export default class extends Component {
  view = _ => `_html:<h1 id="do-it-youself">Do It Youself</h1>
<p>Instead of following the out-of-box configuration schema, you can create the <strong>src/index.tsx</strong> of your own without using the <strong>app.start</strong> function.</p>
<p>In this case, you will need to know how to create the layout, pages, and components, which is easy to do.</p>
<h2 id="create-layout">Create Layout</h2>
<p>You can create the layout as a regular <a href="#component">component</a> and render it before rendering pages. You can set up title, menus and nav whatever you want freely.</p>
<h2 id="create-pages">Create Pages</h2>
<p>The auto generated event-component mapping in <strong>src/_lib/index.tsx</strong> makes it very easy to create the pages, and sets the routing events to the pages in a generic way.</p>
<pre><code class="language-javascript">import app from 'apprun';
import Layout from './layout';
import pages from './_lib';

app.render(document.body, &lt;Layout /&gt;);

const element = 'main';
pages.forEach(def =&gt; {
  const [e, Comp] = def;
  const component = new Comp().mount(element);
  app.on(e, (...p) =&gt; component.run('.', ...p));
});
</code></pre>
<blockquote>
<p>Note: the . event is the <em>refresh</em> event built-in every AppRun component. It makes components render themselves.</p>
</blockquote>
<h2 id="create-components">Create Components</h2>
<p>Components are the application building blocks in AppRun applications.</p>
<p>Pages are <a href="#components">AppRun components</a> in the <strong>src/pages</strong> directory. They auto indexed and built into the application.</p>
<p>Pages use components as building blocks. You can put non-page components in the <strong>src/components</strong> directory. And import them where needed.</p>
<p>You can learn more about the components <a href="#components">here</a>.</p>
`;
}