import { app, Component } from 'apprun';
export default class extends Component {
  view = _ => `_html:<h1 id="components">Components</h1>
<p>Components are the application building blocks in AppRun applications.</p>
<p>An AppRun component  has the elm architecture, which means inside a component, there are <em>state</em>, <em>view</em>, and <em>update</em>. Basically, components provide a local scope.</p>
<h2 id="create-component">Create Component</h2>
<p>It is straightforward to create a component. You create a component class around the <em>state</em>, <em>view</em>, and <em>update</em>.</p>
<pre><code class="language-javascript">import {app, Component} from 'apprun';

class Counter extends Component {
  state = '';
  view = state =&gt; &lt;div/&gt;;
  update = {};
}
</code></pre>
<h2 id="mount-component-to-element">Mount Component to Element</h2>
<p>Then you mount the component instance to an element.</p>
<pre><code class="language-javascript">const element = 'main';
new Counter().mount(element);
</code></pre>
<p>The component can be mounted to the web page element or element ID. When the component is mounted to an <em>element ID</em>, It will not render the element if it cannot find it.</p>
<h2 id="child-components">Child Components</h2>
<p>Beside mounting the components, you can also have nested components by using the capitalized JSX tag to create AppRun Components in JSX.</p>
<pre><code class="language-javascript">class Child extends Component {
  state = {}
  view = state =&gt; &lt;div&gt;&lt;/div&gt;
  update = {}
}

class Parent extends Component {
  state = {}
  view = state =&gt; &lt;div&gt;
    &lt;Child /&gt;
  &lt;/div&gt;
  update = {}
}
</code></pre>
<p>Using components is a technique to decompose the large system into smaller, manageable, and reusable pieces.</p>
<p>Components can be easily turned into <a href="#web-components">web components</a> and use in the HTML pages and markdown pages.</p>
`;
}